* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;
  color: white;
}

.App {
  min-height: 100vh;
  position: relative;
  background-color: #313131;
}

.camera {
  position: relative;
}

.result {
  /* position: fixed;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100%; */
  display: flex;
  align-items: center;
  background-color: rgba(0,0,0,0.8);
  transition: 0.4s;
}

.result.hasPhoto {
  left: 0; 
}

video {
  width: 100%;
  max-width: 100%;
  height: auto;
}

button{
  appearance: none;
  border: none;
  outline: none;

  padding: 8px 16px;
  background-image: linear-gradient(to right, #62a1c9 50%, #fc3 50%);
  background-size: 200%;
  color: #fff;
  font-size: 24px;
  font-weight: 700;
  transition: 0.4s;
  cursor: pointer;
}

button#close {
  position: absolute;
  bottom: 20px;
  left: 20px;
}

button#submit {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

button:hover {
  background-position: 100%;
}

canvas {
  width: 100%;
  height: auto;
}

#center {
  position:absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  width: 50%;
  display: block;
  margin: auto;
}

input {
  color: black;
  height: 5vh;
  text-align: center;
  font-size: 25px;
  margin-bottom: 1vh;
}

stats{ 
  color: white;
}

